'use client';

import { Card } from '@/components/ui/Card';
import { cn } from '@/lib/cn';
import { TrendingUp, TrendingDown, Minus } from 'lucide-react';

interface StatCardProps {
  title: string;
  value: number;
  change?: number;
  format?: 'number' | 'currency' | 'compact';
  icon?: React.ReactNode;
  delay?: number;
}

function formatValue(value: number, format: string): string {
  switch (format) {
    case 'currency':
      return new Intl.NumberFormat('en-US', {
        style: 'currency',
        currency: 'USD',
      }).format(value);
    case 'compact':
      return new Intl.NumberFormat('en-US', {
        notation: 'compact',
        maximumFractionDigits: 1,
      }).format(value);
    default:
      return new Intl.NumberFormat('en-US').format(value);
  }
}

export function StatCard({
  title,
  value,
  change = 0,
  format = 'number',
  icon,
  delay = 0,
}: StatCardProps) {
  const isPositive = change > 0;
  const isNegative = change < 0;
  const isNeutral = change === 0;

  return (
    <Card
      className="animate-fade-in"
      style={{ animationDelay: `${delay * 50}ms` }}
    >
      <div className="flex items-start justify-between">
        <div className="space-y-2">
          <p className="text-sm text-text-secondary">{title}</p>
          <p className="text-2xl font-semibold text-text-primary animate-count">
            {formatValue(value, format)}
          </p>
        </div>
        {icon && (
          <div className="p-2 bg-accent/10 rounded-lg text-accent">{icon}</div>
        )}
      </div>

      {/* Change indicator */}
      <div className="mt-3 flex items-center gap-1.5">
        {isPositive && (
          <>
            <TrendingUp className="w-4 h-4 text-success" />
            <span className="text-sm text-success">+{change}%</span>
          </>
        )}
        {isNegative && (
          <>
            <TrendingDown className="w-4 h-4 text-error" />
            <span className="text-sm text-error">{change}%</span>
          </>
        )}
        {isNeutral && (
          <>
            <Minus className="w-4 h-4 text-text-tertiary" />
            <span className="text-sm text-text-tertiary">No change</span>
          </>
        )}
        <span className="text-xs text-text-muted ml-1">vs last week</span>
      </div>
    </Card>
  );
}

