  'use client';

import Link from 'next/link';
import { Card, CardHeader, CardTitle } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';
import { Button } from '@/components/ui/Button';
import { cn } from '@/lib/cn';
import { ArrowRight, Clock, Zap } from 'lucide-react';
import { formatDistanceToNow } from 'date-fns';

interface Run {
  id: string;
  agentName: string;
  status: 'running' | 'completed' | 'failed';
  input: string;
  tokens: number;
  duration: number | null;
  startedAt: Date;
}

const recentRuns: Run[] = [
  {
    id: 'run_1',
    agentName: 'Research Agent',
    status: 'completed',
    input: 'Analyze the latest trends in WebGPU...',
    tokens: 3421,
    duration: 4200,
    startedAt: new Date(Date.now() - 2 * 60 * 1000),
  },
  {
    id: 'run_2',
    agentName: 'Code Assistant',
    status: 'running',
    input: 'Refactor the authentication module...',
    tokens: 1250,
    duration: null,
    startedAt: new Date(Date.now() - 30 * 1000),
  },
  {
    id: 'run_3',
    agentName: 'Data Analyst',
    status: 'completed',
    input: 'Generate a report on Q4 sales...',
    tokens: 5680,
    duration: 8500,
    startedAt: new Date(Date.now() - 5 * 60 * 1000),
  },
  {
    id: 'run_4',
    agentName: 'Content Writer',
    status: 'failed',
    input: 'Write a blog post about AI agents...',
    tokens: 890,
    duration: 2100,
    startedAt: new Date(Date.now() - 10 * 60 * 1000),
  },
  {
    id: 'run_5',
    agentName: 'Research Agent',
    status: 'completed',
    input: 'Compare React and Vue frameworks...',
    tokens: 2340,
    duration: 3800,
    startedAt: new Date(Date.now() - 15 * 60 * 1000),
  },
];

const statusConfig = {
  running: { variant: 'info' as const, label: 'Running', pulse: true },
  completed: { variant: 'success' as const, label: 'Completed', pulse: false },
  failed: { variant: 'error' as const, label: 'Failed', pulse: false },
};

export function RecentRuns() {
  return (
    <Card padding="none">
      <CardHeader className="px-4 pt-4">
        <CardTitle>Recent Runs</CardTitle>
        <Link href="/runs">
          <Button variant="ghost" size="sm" className="gap-1">
            View all
            <ArrowRight className="w-4 h-4" />
          </Button>
        </Link>
      </CardHeader>

      <div className="divide-y divide-border-subtle">
        {recentRuns.map((run) => {
          const config = statusConfig[run.status];
          return (
            <Link
              key={run.id}
              href={`/runs/${run.id}`}
              className="flex items-center gap-4 px-4 py-3 hover:bg-bg-hover transition-colors"
            >
              {/* Status indicator */}
              <div
                className={cn(
                  'w-2 h-2 rounded-full',
                  run.status === 'running' && 'bg-info pulse-live',
                  run.status === 'completed' && 'bg-success',
                  run.status === 'failed' && 'bg-error'
                )}
              />

              {/* Content */}
              <div className="flex-1 min-w-0">
                <div className="flex items-center gap-2">
                  <span className="font-medium text-sm text-text-primary">
                    {run.agentName}
                  </span>
                  <Badge variant={config.variant} size="sm" pulse={config.pulse}>
                    {config.label}
                  </Badge>
                </div>
                <p className="text-sm text-text-secondary truncate mt-0.5">
                  {run.input}
                </p>
              </div>

              {/* Meta */}
              <div className="flex items-center gap-4 text-xs text-text-tertiary">
                <div className="flex items-center gap-1">
                  <Zap className="w-3.5 h-3.5" />
                  {run.tokens.toLocaleString()}
                </div>
                {run.duration && (
                  <div className="flex items-center gap-1">
                    <Clock className="w-3.5 h-3.5" />
                    {(run.duration / 1000).toFixed(1)}s
                  </div>
                )}
                <span className="text-text-muted">
                  {formatDistanceToNow(run.startedAt, { addSuffix: true })}
                </span>
              </div>
            </Link>
          );
        })}
      </div>
    </Card>
  );
}

