'use client';

import { Card, CardHeader, CardTitle } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';
import { cn } from '@/lib/cn';
import { Check, AlertTriangle, X } from 'lucide-react';

interface Service {
  name: string;
  status: 'up' | 'degraded' | 'down';
  latency?: number;
}

const services: Service[] = [
  { name: 'LLM Backends', status: 'up', latency: 120 },
  { name: 'Memory Store', status: 'up', latency: 8 },
  { name: 'Sandbox', status: 'up', latency: 45 },
  { name: 'Workflows', status: 'up', latency: 15 },
];

const statusConfig = {
  up: {
    icon: Check,
    color: 'text-success',
    bg: 'bg-success/10',
    label: 'Operational',
  },
  degraded: {
    icon: AlertTriangle,
    color: 'text-warning',
    bg: 'bg-warning/10',
    label: 'Degraded',
  },
  down: {
    icon: X,
    color: 'text-error',
    bg: 'bg-error/10',
    label: 'Down',
  },
};

export function SystemHealth() {
  const allUp = services.every((s) => s.status === 'up');
  const someDown = services.some((s) => s.status === 'down');
  const overallStatus = someDown ? 'down' : allUp ? 'up' : 'degraded';
  const overallConfig = statusConfig[overallStatus];

  return (
    <Card padding="none">
      <CardHeader className="px-4 pt-4">
        <CardTitle>System Health</CardTitle>
        <Badge
          variant={
            overallStatus === 'up'
              ? 'success'
              : overallStatus === 'degraded'
              ? 'warning'
              : 'error'
          }
          size="sm"
        >
          {overallConfig.label}
        </Badge>
      </CardHeader>

      <div className="px-4 pb-4 space-y-2">
        {services.map((service) => {
          const config = statusConfig[service.status];
          const Icon = config.icon;

          return (
            <div
              key={service.name}
              className="flex items-center justify-between py-2"
            >
              <div className="flex items-center gap-2">
                <div className={cn('p-1 rounded', config.bg)}>
                  <Icon className={cn('w-3.5 h-3.5', config.color)} />
                </div>
                <span className="text-sm text-text-primary">{service.name}</span>
              </div>

              {service.latency && (
                <span className="text-xs text-text-tertiary">
                  {service.latency}ms
                </span>
              )}
            </div>
          );
        })}
      </div>
    </Card>
  );
}

