'use client';

import Link from 'next/link';
import { Card, CardHeader, CardTitle } from '@/components/ui/Card';
import { Badge } from '@/components/ui/Badge';
import { Button } from '@/components/ui/Button';
import { cn } from '@/lib/cn';
import { ArrowRight, Bot } from 'lucide-react';

interface Agent {
  id: string;
  name: string;
  model: string;
  status: 'online' | 'busy' | 'offline';
  runningTasks: number;
}

const agents: Agent[] = [
  {
    id: 'agent_1',
    name: 'Research Agent',
    model: 'gpt-4o',
    status: 'busy',
    runningTasks: 2,
  },
  {
    id: 'agent_2',
    name: 'Code Assistant',
    model: 'claude-3-5-sonnet',
    status: 'online',
    runningTasks: 0,
  },
  {
    id: 'agent_3',
    name: 'Data Analyst',
    model: 'gpt-4o-mini',
    status: 'online',
    runningTasks: 0,
  },
  {
    id: 'agent_4',
    name: 'Content Writer',
    model: 'claude-3-5-sonnet',
    status: 'offline',
    runningTasks: 0,
  },
];

const statusConfig = {
  online: { color: 'bg-success', label: 'Online' },
  busy: { color: 'bg-warning', label: 'Busy' },
  offline: { color: 'bg-text-tertiary', label: 'Offline' },
};

export function ActiveAgents() {
  return (
    <Card padding="none">
      <CardHeader className="px-4 pt-4">
        <CardTitle>Agents</CardTitle>
        <Link href="/agents">
          <Button variant="ghost" size="sm" className="gap-1">
            View all
            <ArrowRight className="w-4 h-4" />
          </Button>
        </Link>
      </CardHeader>

      <div className="divide-y divide-border-subtle">
        {agents.map((agent) => {
          const config = statusConfig[agent.status];
          return (
            <Link
              key={agent.id}
              href={`/agents/${agent.id}`}
              className="flex items-center gap-3 px-4 py-3 hover:bg-bg-hover transition-colors"
            >
              {/* Icon */}
              <div
                className={cn(
                  'w-8 h-8 rounded-lg flex items-center justify-center',
                  agent.status === 'online' && 'bg-success/10',
                  agent.status === 'busy' && 'bg-warning/10',
                  agent.status === 'offline' && 'bg-bg-elevated'
                )}
              >
                <Bot
                  className={cn(
                    'w-4 h-4',
                    agent.status === 'online' && 'text-success',
                    agent.status === 'busy' && 'text-warning',
                    agent.status === 'offline' && 'text-text-tertiary'
                  )}
                />
              </div>

              {/* Info */}
              <div className="flex-1 min-w-0">
                <div className="flex items-center gap-2">
                  <span className="font-medium text-sm text-text-primary">
                    {agent.name}
                  </span>
                  <span
                    className={cn(
                      'w-1.5 h-1.5 rounded-full',
                      config.color,
                      agent.status === 'busy' && 'animate-pulse'
                    )}
                  />
                </div>
                <p className="text-xs text-text-tertiary">{agent.model}</p>
              </div>

              {/* Tasks */}
              {agent.runningTasks > 0 && (
                <Badge variant="info" size="sm">
                  {agent.runningTasks} running
                </Badge>
              )}
            </Link>
          );
        })}
      </div>
    </Card>
  );
}

